
.select {
  background: #fff;
}
.select .swiper-wrapper {
  height: 200px;
}
.select .swiper-wrapper .swiper-slide img {
  width: 100%;
  height: 200px;
}
.select .news {
  min-height: 500px;
  padding: 0 10px;
}
.select .news .new {
  height: 100px;
  color: #262627;
  border-bottom: 1px solid #eee;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
}
.select .news .new img {
  height: 80px;
  width: 100px;
}
.select .news .new .intro {
  width: 80%;
  height: 80px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 10px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.select .news .new .intro h4 {
  font-size: 20px;
  line-height: 1.2;
  font-weight: bold;
  overflow: hidden;
  display: -webkit-box;
  word-break: break-all;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
}
.select .news .new .intro p {
  font-size: 13px;
  color: #666;
}
.select .news .loadMore {
  height: 50px;
  width: 100%;
  color: #545454;
  background: #eee;
  text-align: center;
  line-height: 50px;
  font-size: 13px;
  border: none;
  border-radius: 0;
  outline: none;
  margin-bottom: 10px;
}
.select .news .fail {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 300px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
/*加载动画*/
.spinner {
  position: fixed;
  left: 40%;
  bottom: 10%;
  width: 80px;
  height: 80px;
  margin: 50px auto;
  background-color: #69c61d;
  border-radius: 100%;
  -webkit-animation: scaleout 1s infinite ease-in-out;
  animation: scaleout 1s infinite ease-in-out;
}
@-webkit-keyframes scaleout {
0% {
    -webkit-transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
    opacity: 0;
}
}
@-webkit-keyframes scaleout {
0% {
    transform: scale(0);
    -webkit-transform: scale(0);
}
100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    opacity: 0;
}
}
@keyframes scaleout {
0% {
    transform: scale(0);
    -webkit-transform: scale(0);
}
100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    opacity: 0;
}
}


